.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-size: cover;
    background-position: center;
    padding: 20px;
}

.mapContainer {
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 20px;
    /* 增加地圖容器和其他元素之間的間距 */
}

.mapImageContainer {
    position: relative;
}

.mapImage {
    width: 100%;
    max-width: 600px;
    height: auto;
}

.map-buttons-container {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
}

.map-toggle-button,
.input-toggle-button {
    padding: 10px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease;
    margin-bottom: 10px;

    &:hover {
        background-color: #45a049;
    }

    svg {
        font-size: 18px;
    }
}

.input-toggle-button {
    margin-bottom: 0;
}

.audioButtonsContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 10px;
}

.audio-button {
    padding: 10px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: #45a049;
    }

    svg {
        font-size: 24px;
        /* 图标大小可以根据需要调整 */
    }
}

.resultContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    position: relative;
    /* 将输入框容器向下移动 */
}

.input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 200px;
    margin-bottom: 10px;
    /* 與按鈕間距 */
}

.end-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    white-space: nowrap;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: #45a049;
    }
}

@media (max-width: 768px) {
    .mapImage {
        width: 70%;
    }

    .map-toggle-button,
    .input-toggle-button,
    .audio-button {
        padding: 8px 16px;
        font-size: 14px;
    }

    .input {
        width: 150px;
    }
}

@media (max-width: 480px) {
    .mapImage {
        width: 100%;
    }

    .map-toggle-button,
    .input-toggle-button,
    .audio-button {
        padding: 6px 12px;
        font-size: 12px;
    }

    .input {
        width: 100px;
    }
}